.brand{
  font-weight:700;
  font-size:20px
}
.site-header{
  position:relative;
  background-color: transparent;/*#EAECF2*/
}
.site-header__wrapper{padding-top:1rem;padding-bottom:1rem}
@media(min-width:600px){
  .site-header__wrapper{
    display:flex;
    justify-content:space-between;
    align-items: center;
    padding-top:0;
    padding-bottom:0;
  }
}

@media(min-width:600px){
  .nav__wrapper{display:flex}
}
@media(max-width:599px){
  .nav__wrapper{
    position:absolute;
    top:100%;
    right:0;
    left:0;
    z-index:1000;
    background-color:#bdd0f3;
    visibility:hidden;
    opacity:0;
    transform:translateY(-100%);
    transition:transform .3s ease-out,opacity .3s ease-out
  }
  .nav__wrapper.active{
    visibility:visible;
    opacity:1;
    transform:translateY(0)
  }
}
.nav__item {
  list-style: none;

}
.nav__item a{
  display:block;
  padding:1.5rem 1rem;
  font-weight: 600;
}
.nav__item span{
  display: block;
  padding:1.5rem 1rem;
  cursor: pointer;
  color: #d9f0f7;
  border: 2px solid #d9f0f7fd;
  background-color: rgb(31, 39, 117);
  font-weight: 700;
  color: #c1cff8;
}
.nav__toggle{display:none}

@media(max-width:599px){
  .nav__toggle{
    display:block;
    position:absolute;
    right:1rem;
    top:1rem;
    font-weight: 700;
    background-color:rgb(31, 39, 117);;
    padding: 3px;
    color: white;
    border: 1px double green;
    border-radius: 2px;
  }
}